.navLogo{
    position: fixed;
    top:3vh;
    left:5vh;
    height: 10vh;
}


.button {
    position: relative;
    color: black;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 24px;
    display: inline-block;
    padding: 15px 30px;
    letter-spacing: 1px;
    overflow: hidden;
    transition: 0.5s;
    color: red;
}

.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    padding: 30px;
    background-color: rgba($color: #293133, $alpha: 0.95);
    color: seashell;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
 
}

.header_section {
    display: flex;
    align-items: center;
    padding: 0;
}

.header_item {
    padding: 15px 15px;
    font-size: 20px;
    margin-left: 10px;
}

.headerlogo img {
    width: 260px;
    height: 150px;
    padding-left: 0 px;
}

.button_line {
    position: absolute;
    display: block;
}

.button_line-top {
    top: 0;
    left: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg, transparent, red);
}
.button:hover{
    transition: all;
    color:red;
}
.button:hover .button_line-top {
    left: 100%;
    transition: 1s;
    transition-delay: 0.25s;
}

.button_line-bottom {
    bottom: 0;
    right: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(270deg, transparent, red);
}

.button:hover .button_line-bottom {
    right: 100%;
    transition: 1s;
    transition-delay: 0.25s;
}

.button_line-left {
    bottom: -100%;
    left: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(0deg, transparent, red);
}

.button:hover .button_line-left {
    bottom: 100%;
    transition: 1s;
    transition-delay: 0s;
}

.button_line-right {
    top: -100%;
    right: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(1800deg, transparent, red);
}

.button:hover .button_line-right {
    top: 100%;
    transition: 1s;
    transition-delay: 0s;
}

